.cta-open-wrapper {
  position: fixed;
  bottom: 30px;
  right: 15px;
  z-index: 10;
  text-align: center;
}
@media screen and (min-width: 997px) and (max-width: 1500px) {
  .cta-open-wrapper {
    bottom: 15px;
  }
}
.cta-tooltip {
  width: 130px;
  padding: 22px 14px 19px 14px;
  position: relative;
  background-color: #fff;
  box-shadow: 0 0 10px 0 rgba(0,0,0,0.2);
  margin-bottom: 25px;
}
.cta-tooltip:after {
  content: '';
  display: block;
  top: 100%;
  left: 50%;
  border: solid transparent;
  height: 0;
  width: 0;
  position: absolute;
  border-color: transparent;
  border-top-color: #fff;
  border-width: 10px;
  margin-left: -10px;
}
.cta-main-title {
  color: #008000;
  font-size: 20px;
  margin-bottom: 10px;
}
.cta-main-text {
  color: #e1e1e1;
  font-size: 12px;
  margin-bottom: 15px;
}
.cta-open {
  color: $ctaButtonColor;
  background-color: $ctaButtonBackground;
}
.cta-open:hover {
  background-color: $ctaButtonHoverBackground;
  color: $ctaButtonHoverColor;
}
.phone-icon {
  background-color: #008000;
  box-shadow: 0 0 0 7px rgba(0,128,0,0.3);
  font-size: 20px;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  border: 0;
  position: relative;
}
.phone-icon i,
.phone-icon img {
  color: #fff;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 50%;
  transform: translateY(-50%);
}
.phone-icon:after {
  content: attr(data-notifications);
  padding: 3px 6px;
  font-size: 12px;
  color: #fff;
  background-color: #f00;
  border-radius: 50%;
  position: absolute;
  top: -6px;
  right: -6px;
}
.cta-hide-tooltip {
  position: absolute;
  top: -8px;
  right: -8px;
  border-radius: 50%;
  font-size: 16px;
  border: 1px solid #00e600;
  background-color: #fff;
  color: #008000;
}
.cta-open {
  width: 100%;
  height: 30px;
  border: 2px solid #008000;
  color: #fff;
  background-color: #008000;
  transition: all 0.3s ease-in-out;
}
.cta-open:hover {
  background-color: #004d00;
  color: #fff;
  border-color: #004d00;
}
.cta-overlay {
  background-color: rgba(225,225,225,0.9);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 11;
}
.form-wrapper-all {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  max-width: 500px;
  background-color: #fff;
  box-shaddow: 0 0 10px 0 rgba(0,0,0,0.2);
}
